








































































import { Vue, Component, Watch } from "vue-property-decorator";
import { throttle } from "lodash";
import { gsap, Power0, Power1 } from "gsap";
import isMobile from "ismobilejs";

const isAnyMobile = isMobile().any;
const mouseAnimationDuration = isAnyMobile ? 0.5 : 0.75;
const loopThrottle = isAnyMobile ? 200 : 60;
const powerFunc = isAnyMobile ? Power0.easeInOut : Power1.easeOut;
let startTime: number;

const animationData = {
  circle: {
    factor: isAnyMobile ? 0.1 : 1.0,
  },
  diamond1: {
    factor: isAnyMobile ? 0.5 : 1.0,
  },
  diamond2: {
    factor: isAnyMobile ? 0.5 : 1.0,
  },
  diamond3: {
    factor: isAnyMobile ? 0.3 : 1.0,
  },
};

@Component
class Ava extends Vue {
  mouse = {
    x: 0.5,
    y: 0,
  };

  onMousemove(e: MouseEvent) {
    this.mouse = {
      x: (e.clientX / window.innerWidth) * 2 - 1, // Lerpppp
      y: -(e.clientY / window.innerHeight) * 2 + 1,
    };
  }

  get isActive() {
    return this.$store.state.scrollState.currentSection === 0;
  }

  moveThings() {
    const circle = this.$refs.circle as SVGPathElement;
    const diamond1 = this.$refs.diamond1 as SVGRectElement;
    const diamond2 = this.$refs.diamond2 as SVGRectElement;
    const diamond3 = this.$refs.diamond3 as SVGRectElement;

    gsap.to(circle, {
      duration: mouseAnimationDuration,
      x: this.mouse.x * 8 * animationData.circle.factor,
      y: this.mouse.y * 15 * animationData.circle.factor,
      ease: powerFunc,
    });

    gsap.to(diamond1, {
      duration: mouseAnimationDuration,
      x: -this.mouse.x * 30 * animationData.diamond1.factor,
      y: -this.mouse.y * 10 * animationData.diamond1.factor,
      ease: powerFunc,
    });

    gsap.to(diamond2, {
      duration: mouseAnimationDuration,
      x: -this.mouse.x * 30 * animationData.diamond2.factor,
      y: this.mouse.y * 15 * animationData.diamond2.factor,
      ease: powerFunc,
    });

    gsap.to(diamond3, {
      duration: mouseAnimationDuration,
      x: this.mouse.x * 50 * animationData.diamond3.factor,
      y: this.mouse.y * 25 * animationData.diamond3.factor,
      ease: powerFunc,
    });
  }

  get boundOnMouseMove() {
    return this.onMousemove.bind(this);
  }
  get moveThingsFunc() {
    return throttle(this.moveThings, loopThrottle).bind(this);
  }

  get isAppInitted() {
    return this.$store.state.isInitted;
  }

  @Watch("isActive")
  onIsActiveChange(newVal: boolean) {
    if (!newVal) {
      document.removeEventListener("mousemove", this.boundOnMouseMove);
    } else {
      document.addEventListener("mousemove", this.boundOnMouseMove);
    }
  }

  loop(timestamp?: number) {
    if (this.isActive) {
      if (isAnyMobile && timestamp) {
        if (!startTime) startTime = timestamp;
        const elapsed = timestamp - startTime;
        // Simulate mouse event
        this.mouse.x = (Math.sin(elapsed * 0.001) - 0.5) * 2;
        this.mouse.y = (Math.cos(elapsed * 0.001) - 0.5) * 2;
      }
      this.moveThingsFunc();
    }

    requestAnimationFrame(this.loop);
  }

  @Watch("isAppInitted")
  onAppInitted(isInitted: boolean) {
    if (!isInitted) return;

    const entranceTimeline = gsap.timeline({
      onComplete: () => {
        this.initInteractivity();
      },
    });
    entranceTimeline.fromTo(
      this.$refs.diamondGroup,
      {
        opacity: 0,
        y: 80,
      },
      {
        duration: 1,
        opacity: 1,
        y: 0,
        stagger: 0.1,
      }
    );

    entranceTimeline.fromTo(
      this.$refs.centerGroup,
      {
        opacity: 0,
      },
      {
        opacity: 1,
      },
      "-=0.85"
    );

    entranceTimeline.call(
      () => this.$emit("animationFinished", true),
      undefined,
      "-=0.4"
    );
  }
  initInteractivity() {
    this.loop();
    if (isAnyMobile) return;
    document.addEventListener("mousemove", this.boundOnMouseMove);
  }
  beforeDestroy() {
    if (isAnyMobile) return;
    document.removeEventListener("mousemove", this.boundOnMouseMove);
  }
}

export default Ava;
