




















































































import Preloader from "@/components/common/Preloader.vue";
import Guide from "@/components/common/Guide.vue";
import Intro from "@/components/home/Intro.vue";
import MyWork from "@/components/home/MyWork.vue";
import ContactMe from "@/components/home/ContactMe.vue";
import IntroCanvas from "@/components/home/IntroCanvas.vue";
import MyWorkCanvas from "@/components/home/MyWorkCanvas.vue";
import { projects } from "@/configs/projects";
import { Component, Vue, Watch } from "vue-property-decorator";
import isMobile from "ismobilejs";

@Component({
  components: {
    IntroCanvas,
    Intro,
    Guide,
    MyWork,
    ContactMe,
    Preloader,
    MyWorkCanvas,
  },
})
class Home extends Vue {
  // debug = "";

  updateScrollState() {
    const sections = this.$refs.sections as HTMLElement[];

    let currentSection = 0;
    let sectionProgress = 0.0;
    const sectionCount = sections.length;
    const offset = 30;

    for (let index = sectionCount - 1; index >= 0; index--) {
      const section = sections[index];
      const elRect = section.getBoundingClientRect();
      const elTop = elRect.top;

      if (elTop - offset < 0) {
        currentSection = index;
        sectionProgress = Math.abs(elTop) / elRect.height;
        break;
      }
    }

    // this.debug = currentSection + " - " + sectionProgress;

    this.$store.commit("setScrollState", {
      currentSection,
      sectionProgress,
    });
  }
  get updateScrollStateFunc() {
    return this.updateScrollState.bind(this);
  }
  get isInitted() {
    return this.$store.state.isInitted;
  }
  get isIOS() {
    return isMobile().apple.phone;
  }
  projects = projects;

  @Watch("isInitted")
  onInitted(newVal: boolean) {
    if (this.isIOS && newVal) {
      require("inobounce");
    }
  }

  loopId?: number;

  loop() {
    this.updateScrollStateFunc();
    requestAnimationFrame(this.loop);
  }

  mounted() {
    window.addEventListener("scroll", this.updateScrollStateFunc);
    if (this.isIOS) {
      this.loop();
    }
  }
  beforeDestroy() {
    window.removeEventListener("scroll", this.updateScrollStateFunc);
    if (this.isIOS && this.loopId) {
      cancelAnimationFrame(this.loopId);
    }
  }
}
export default Home;
